import React, { useState, useEffect, useRef } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Loader2, Mail, CheckCircle, AlertCircle, X } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

interface GmailOAuthModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSuccess: (data: any) => void;
  userId?: string;
  redirectPath?: string;
}

const GmailOAuthModal: React.FC<GmailOAuthModalProps> = ({
  isOpen,
  onClose,
  onSuccess,
  userId,
  redirectPath
}) => {
  const [status, setStatus] = useState<'idle' | 'loading' | 'success' | 'error'>('idle');
  const [message, setMessage] = useState('');
  const iframeRef = useRef<HTMLIFrameElement>(null);

  const generateGmailOAuthProxyUrl = () => {
    const baseUrl = import.meta.env.VITE_APP_BASE_URL || 'http://localhost:5001';
    const currentPath = redirectPath || window.location.hash.replace('#', '') || '/webhooks';

    return `${baseUrl}/api/gmail/auth/proxy?userId=${userId || '1'}&redirectPath=${encodeURIComponent(currentPath)}`;
  };

  const handleConnect = async () => {
    setStatus('loading');
    setMessage('Opening Gmail authentication...');

    try {
      // Use backend API to generate auth URL with proper authentication
      const response = await axiosInstance.get('/api/gmail/auth/url', {
        params: {
          redirectPath: 'popup' // Use 'popup' to indicate this is for popup flow
        }
      });

      if (!response.data.success) {
        throw new Error(response.data.message || 'Failed to generate Gmail auth URL');
      }

      console.log('üîÑ Opening Gmail OAuth in popup window');

      // Open Gmail OAuth in popup window
      const popup = window.open(
        response.data.data.authUrl,
        'gmail-oauth',
        'width=600,height=700,scrollbars=yes,resizable=yes,status=yes,location=yes,toolbar=no,menubar=no'
      );

      if (!popup) {
        throw new Error('Popup blocked. Please allow popups for this site.');
      }

      // Monitor popup for closure
      const checkClosed = setInterval(() => {
        if (popup.closed) {
          clearInterval(checkClosed);
          if (status === 'loading') {
            setStatus('error');
            setMessage('Authentication was cancelled. Please try again.');
          }
        }
      }, 1000);

      // Set timeout for popup
      setTimeout(() => {
        if (!popup.closed) {
          popup.close();
          clearInterval(checkClosed);
          if (status === 'loading') {
            setStatus('error');
            setMessage('Authentication timeout. Please try again.');
          }
        }
      }, 300000); // 5 minutes timeout

    } catch (error: any) {
      console.error('Gmail OAuth error:', error);
      setStatus('error');
      setMessage(error.message || 'Failed to initiate Gmail OAuth');
    }
  };

  // Listen for messages from the OAuth popup
  useEffect(() => {
    const handleMessage = (event: MessageEvent) => {
      // Verify origin for security
      if (event.origin !== window.location.origin) {
        return;
      }

      console.log('üîç Received message from popup:', event.data);

      if (event.data.type === 'GMAIL_AUTH_SUCCESS') {
        console.log('üéâ Received Gmail OAuth success from popup:', event.data);
        setStatus('success');
        setMessage('Gmail connected successfully!');
        toast.success(`Gmail connected successfully! (${event.data.data.email})`);

        // Call the success callback to update parent component
        onSuccess(event.data.data);

        // Close modal after a short delay
        setTimeout(() => {
          onClose();
        }, 1500);
      } else if (event.data.type === 'GMAIL_AUTH_ERROR') {
        console.log('‚ùå Received Gmail OAuth error from popup:', event.data);
        setStatus('error');
        setMessage(event.data.message || 'Failed to connect Gmail');
        toast.error(event.data.message || 'Failed to connect Gmail');

        // Close modal after error display
        setTimeout(() => {
          onClose();
        }, 3000);
      }
    };

    window.addEventListener('message', handleMessage);

    return () => {
      window.removeEventListener('message', handleMessage);
    };
  }, [onSuccess, onClose, status]);

  // Reset status when modal opens/closes
  useEffect(() => {
    if (isOpen) {
      setStatus('idle');
      setMessage('');
    }
  }, [isOpen]);

  const handleClose = () => {
    setStatus('idle');
    setMessage('');
    // Clear iframe src when closing
    if (iframeRef.current) {
      iframeRef.current.src = 'about:blank';
    }
    onClose();
  };

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="max-w-2xl max-h-[80vh]">
        <DialogHeader>
          <DialogTitle className="flex items-center gap-2">
            <Mail className="h-5 w-5 text-blue-600" />
            Connect Gmail Account
          </DialogTitle>
          <DialogDescription>
            Connect your Gmail account to enable email automation in your workflows
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-4">
          {status === 'idle' && (
            <div className="text-center space-y-4">
              <div className="p-6 bg-blue-50 rounded-lg border border-blue-200">
                <Mail className="h-12 w-12 text-blue-600 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-gray-900 mb-2">
                  Gmail Integration
                </h3>
                <p className="text-sm text-gray-600 mb-4">
                  By connecting your Gmail account, you'll be able to:
                </p>
                <ul className="text-sm text-gray-600 text-left space-y-1 mb-4">
                  <li>‚Ä¢ Read and send emails automatically</li>
                  <li>‚Ä¢ Create email-based workflow triggers</li>
                  <li>‚Ä¢ Access Gmail data in your automations</li>
                </ul>
              </div>
              
              <Button
                onClick={handleConnect}
                className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-white"
              >
                <Mail className="h-4 w-4 mr-2" />
                Connect With Gmail
              </Button>
              
              <p className="text-xs text-gray-500 text-center">
                Please authenticate your Gmail account to allow access to AI CRUITMENT. 
                <a href="#" className="text-blue-600 underline ml-1">View privacy policy</a>
              </p>
            </div>
          )}

          {status === 'loading' && (
            <div className="text-center space-y-6 py-8">
              <div className="flex flex-col items-center space-y-4">
                <div className="relative">
                  <div className="w-20 h-20 bg-blue-100 rounded-full flex items-center justify-center">
                    <Mail className="h-10 w-10 text-blue-600" />
                  </div>
                  <div className="absolute -top-1 -right-1 w-6 h-6 bg-blue-500 rounded-full flex items-center justify-center">
                    <Loader2 className="h-3 w-3 text-white animate-spin" />
                  </div>
                </div>
                <div className="space-y-2">
                  <h3 className="text-xl font-semibold text-gray-900">
                    Redirecting to Gmail
                  </h3>
                  <p className="text-sm text-gray-600 max-w-md">
                    {message}
                  </p>
                </div>
                <div className="p-4 bg-blue-50 rounded-lg border border-blue-200 max-w-md">
                  <div className="flex items-start space-x-3">
                    <div className="flex-shrink-0">
                      <div className="w-6 h-6 bg-blue-600 rounded-full flex items-center justify-center">
                        <span className="text-white text-xs font-bold">!</span>
                      </div>
                    </div>
                    <div className="text-sm text-blue-800">
                      <p className="font-medium mb-1">Secure Authentication</p>
                      <p>You will be redirected to Gmail's secure authentication page. After completing authentication, you'll be returned to your workflow.</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {status === 'success' && (
            <div className="text-center space-y-4">
              <div className="p-6 bg-green-50 rounded-lg border border-green-200">
                <CheckCircle className="h-12 w-12 text-green-600 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-green-900 mb-2">
                  Successfully Connected!
                </h3>
                <p className="text-sm text-green-700">
                  Your Gmail account has been connected successfully. You can now use Gmail in your workflows.
                </p>
              </div>
            </div>
          )}

          {status === 'error' && (
            <div className="text-center space-y-4">
              <div className="p-6 bg-red-50 rounded-lg border border-red-200">
                <AlertCircle className="h-12 w-12 text-red-600 mx-auto mb-4" />
                <h3 className="text-lg font-semibold text-red-900 mb-2">
                  Connection Failed
                </h3>
                <p className="text-sm text-red-700 mb-4">
                  {message}
                </p>
                <Button
                  onClick={handleConnect}
                  variant="outline"
                  className="border-red-300 text-red-700 hover:bg-red-50"
                >
                  Try Again
                </Button>
              </div>
            </div>
          )}
        </div>
      </DialogContent>
    </Dialog>
  );
};

export default GmailOAuthModal;
